	.header-menu-logo {
		@media (max-width: 991px) {
			display: block;
			@include margin-end(auto);
			text-align: center; 
		}
	}
	.header-logo {
		@media (max-width: 991px) {
			display: block;
			@include margin-end(auto);
			text-align: center; 
		}
		.logo-text {
			display: table !important;
			font-weight: 700;
			font-family: var(--font-heading);
			word-break: break-word;
			text-align: center;
			> span {
				display: table-cell;
				vertical-align: middle;
			}
		}
		.logo-site {
			line-height: 0.8;
			display: inline-block;
			&.waiting {
				padding: 0 !important;
				background-color: transparent !important;
			}
		}
	}
	.desktop-toggle-menu {  
		position: fixed;
		top: 0;
		left: -250px; 
		z-index: 9999;
		height: 100vh;  
		width: 250px;
		overflow-x: hidden;
		overflow-y: auto; 
		background: #333;
		border-top: 2px solid var(--main-color);
		&::-webkit-scrollbar { 
			width: 6px; 
			background: transparent;
			@include border-radius-10;
		} 
		&::-webkit-scrollbar-thumb {
			background: #000000; 
			@include border-radius-10; 
		} 
		.toggle-menu-list { 
			display: block;
			width: 100%;   
			li {
				float: none;
				padding: 0;
				&:not(.dropdown) {
					border-bottom: 1px solid #262626;
					> a { 
						display: block;
						padding: 10px 13px;
					}  
				}
				span.expand {
					height: 100%; 
					width: 30px;
					line-height: 30px; 
					position: static;
					text-align: center;  
					@include transition(transform 0.35s ease-in-out); 
					svg path {
						fill: rgba(255,255,255,.7);
					}
				}  
				a {
					font-size: var(--nav-font-size); 
					position: relative; 
					color: rgba(255,255,255,.7);
				}  
				.dropdown-inner {
					padding: 0 13px;  
					border-bottom: 1px solid #262626;
					align-items: center;
					justify-content: space-between;
					@include flexbox; 
					a {
						padding: 10px 0;  
					} 
				} 
				.dropdown-menu {
					position: static;
					@include opacity(inherit);
					visibility: inherit;  
					border: none;
					background: #090b0c;
					z-index: 1;
					width: 100%;
					padding: 0; 
					display: none;  
					@include box-shadow(none); 
					@include border-radius-reset;  
					@include translate(0, 0); 
					&:before {
						content: none;
					} 
					.dropdown-inner {	
						background: #090b0c; 
						a {
							font-weight: 600;
							font-size: var(--nav-sub-font-size);  
							color: #ffffff;  
						}   
						span.expand svg {
							display: none; 
						}
					}    
					li {
						a {
							color: rgba(255,255,255,.7);
						}
						&:hover { 
							background: #b3b3b3;
							@include box-shadow(inset -2px 0 4px -1px rgba(0,0,0,.6));
							> a { 
								color: #000;
								text-decoration: none; 
							}   
						} 
					}     
				}   
				&.mega-is-hover { 
					> .dropdown-inner span.expand { 
						@include transform(rotate(180deg));
					}    
					.dropdown-menu {
						display: block;   
					}  
				}
				&:hover {
					&:not(.dropdown) {
						background: #b3b3b3;
						@include box-shadow(inset -2px 0 4px -1px rgba(0,0,0,.6));
					} 
					> a { 
						color: rgba(255,255,255,.7); 
					} 
				}
			} 
		}
	} 
	.style-1 {
		.sidemenu-holder .navbar .navbar-collapse .menu-list > li {
			&:first-child {
				@include padding-start(0);
			}
			&:last-child {
				@include padding-end(0);
			}
		}
		&.layout-full > div {
			@include padding-start(0);
			@include padding-end(0);
		}
	}
	.style-2 {
		.header-main {
			padding-top: 25px;
			padding-bottom: 25px;
		}
		.header-icons {
			@media (min-width: 992px) {
				width: 25%;
			}
			> ul > li {
				> a {
					color: var(--main-color-text);
					&:hover {
						color: var(--nav-link-hover-color);
					}
				}
				&.top-cart-holder > .cart-target > .basket {
					color: var(--main-color-text);
					&:hover {
						color: var(--nav-link-hover-color);
					}
				} 
			}
		}
		.table-row {
			@media (min-width: 992px) {
				.header-logo {
					display: table-cell;
					vertical-align: middle;
					width: 25%;
					@include padding-end(15px);
				}
				.horizontal-menu {
					display: table-cell !important;
					vertical-align: middle;
					padding: 0 15px;
					.menu-list {
						text-align: start;
					}
				}
			}
			.header-contact-box {
				float: right;
				white-space: nowrap;
				display: none;
				@media (min-width: 1200px) {
					display: block;
				}
				svg {
					width: 48px;
					float: left;
					@include margin-end(15px); 
					path {
						fill: var(--main-color);
					}
				}
				.info {
					display: inline-block;
					padding: 5px 0;
					span {
						display: block;
						color: var(--nav-link-color);
					}
				}
			}
		}
		.header-navigation {
			background: var(--main-color);
			.table-row .vertical-menu .navigation {
				.head {
					line-height: 32px;
					color: var(--main-color-text);
					@include border-radius-reset;
				}
				.navbar .collapse .main-nav {
					border: 3px solid var(--main-color);
					border-top: none;
				}
			} 
			.searchbox {
				@media (min-width: 992px) {
					width: 50%;
				}
				.navbar-form {
					border-color: var(--white);
				}
			}
		}
		&.layout-full > div {
			@include padding-start(0);
			@include padding-end(0);
		} 
	}
	.style-3 {
		.top-bar.border-top-true {
			.table-row .list-inline > li {
				&.email, &.phone {
					.demo-icon {
						color: var(--text-header-color);
					}
				}
			}
			+ .header-main .searchbox .navbar-form {
				border-color: var(--white);
			} 
		}
		.header-navigation {
			background: var(--main-color);
			.sidemenu-holder {
				position: relative;
				.navbar .navbar-collapse .menu-list > li {
					@include border-end(1px solid var(--border-main-nav));
					> .dropdown-inner > a, > a {
						line-height: 22px;
						padding: 14px 0;
					}
					&:hover {
						background: var(--border-main-nav);
					}
					&:last-child {
						@include border-end(none);
					}
				}
			}
			&.border-top-true {
				border-top: 1px solid var(--border-main-nav);
			}
		}
	}
